.h {
  height: 100vh;
  position: relative;
  background-color: #005aba;
}
.h-wraper {
  width: 100%;
  position: absolute;
  top: 25%;
  height: 55%;
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.h-intro {
  font-size: 25px;
  font-weight: 200;
  color: #fff;
}
.h-name {
  font-size: 60px;
  color: #fff;
}
.h-title {
  height: 50px;
  overflow: hidden;
}
.h-title-wrapper {
  height: 100%;
  animation: move 15s ease infinite alternate;
  -webkit-animation: move 15s ease infinite alternate;
}
@keyframes move {
  25% {
    transform: translateY(-50px);
    -webkit-transform: translateY(-50px);
    -moz-transform: translateY(-50px);
    -ms-transform: translateY(-50px);
    -o-transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
    -webkit-transform: translateY(-100px);
    -moz-transform: translateY(-100px);
    -ms-transform: translateY(-100px);
    -o-transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
    -webkit-transform: translateY(-150px);
    -moz-transform: translateY(-150px);
    -ms-transform: translateY(-150px);
    -o-transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
    -webkit-transform: translateY(-200px);
    -moz-transform: translateY(-200px);
    -ms-transform: translateY(-200px);
    -o-transform: translateY(-200px);
  }
}
.h-title-item {
  height: 50px;
  letter-spacing: 3px;
  font-size: 30px;
  font-weight: bold;
  color: #feb800;
  display: flex;
  justify-content: center;
  align-items: center;
}
.h-desc {
  width: 100%;
  font-size: 22px;
  line-height: 2;
  font-weight: 200;
  color: #fff;
  text-transform: capitalize;
  padding: 0.5rem 1rem;
  text-align: center;
}
.avilable-container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 80px 20px;
}
.avilable {
  display: flex;
  align-items: center;
  justify-content: center;
}
.avilable .hire {
  background-color: #feb800;
  padding: 0.25rem 1rem;
  border-radius: 2rem 0 0 2rem;
  -webkit-border-radius: 2rem 0 0 2rem;
  -moz-border-radius: 2rem 0 0 2rem;
  -ms-border-radius: 2rem 0 0 2rem;
  -o-border-radius: 2rem 0 0 2rem;
  padding-right: 10px;
  color: #222;
  font-weight: 500;
  margin-right: 0.2rem;
}
.avStatus {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding: 0.25rem 1rem;
  border-radius: 0 2rem 2rem 0;
  -webkit-border-radius: 0 2rem 2rem 0;
  -moz-border-radius: 0 2rem 2rem 0;
  -ms-border-radius: 0 2rem 2rem 0;
  -o-border-radius: 0 2rem 2rem 0;
}
.avStatus .status {
  width: 12px;
  height: 12px;
  background-color: greenyellow;
  border: 1px solid #222;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  animation: show 0.75s ease-in-out infinite alternate;
  -webkit-animation: show 0.75s ease-in-out infinite alternate;
}
.avilable .avi {
  color: #222;
  padding-left: 5px;
  animation: show 0.75s ease-in-out infinite alternate;
  -webkit-animation: show 0.75s ease-in-out infinite alternate;
}
@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media screen and (max-width: 940px) {
  .navbar ul li {
    padding: 0;
    margin-right: 1rem;
    margin-left: 1rem;
  }
  .h-intro {
    font-size: 22px;
  }
  .h-wraper {
    height: 60%;
    top: 20%;
  }
  .h-title-wrapper {
    padding: 0.5rem;
  }
  .h-desc {
    font-size: 18px;
  }
  .h-title-item {
    height: 50px;
    letter-spacing: 0;
    font-size: 24px;
  }
  .avilable-container {
    align-items: center;
    justify-content: center;
    padding: 90px 0;
  }
  .avilable {
    margin-top: -1rem;
  }
  .avStatus {
    background-color: #fff;
  }
  .avilable .avi {
    color: #222;
  }
  .avStatus .status {
    border: 1px solid #222;
  }
}
